
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'


export default {
name: 'TopBang',
components: {
ViewBox,
XHeader,
XTable
},
created: function () {
this.getPaihangBang()
},
data () {
return {
paihangList: []
}
},
methods: {
mingciCSS: function (item) {
if (item === 1) {
return 'first'
} else if (item === 2) {
return 'second'
} else if (item === 3) {
return 'third'
} else {
return 'other'
}
},
getPaihangBang () {
this.$api.calculateApi.getCalculateTop100().then(result => {
if (result.data.success) {
this.paihangList = result.data.data
} else {
this.$vux.toast.show({
type: 'text',
text: result.data.message
})
}
})
}
}
}



