
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import {WriteMail, DuoBao, Comming} from './index'
import Send from './sendMailToFuture/WriteMail'
import Program from './Program'


export default {
directives: {
TransferDom
},
components: {
Panel,
'send': Send,
Group,
LoadMore,
ViewBox,
XHeader,
Program,
Popup,
'writeMail': WriteMail,
'duobao': DuoBao
},
methods: {
onImgError (item, $event) {
console.log(item, $event)
},
gotoProgram (item) {
switch (item.program) {
case 'writeMail':
this.currentProgram = WriteMail
break
case 'duobao':
this.currentProgram = Comming
break
case 'suocang':
this.currentProgram = Comming
break
case 'chargePhone':
this.currentProgram = Comming
break
case 'onlineHealth':
this.currentProgram = Comming
break
}
this.showProgram = true
this.programName = item.title
console.log(item.program)
},
backList () {
this.showProgram = false
}
},
data () {
return {
msg: '',
currentProgram: WriteMail,
styleObj: {
'zIndex': 101
},
programName: '',
showProgram: false,
type: '1',
list: [{
src: require('../../assets/activity/sendMail/mail.jpeg'),
fallbackSrc: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
title: '',
desc: '',
program: 'writeMail'
}, {
src: require('../../assets/activity/duobao/duobao.jpeg'),
title: '',
desc: '1ISC',
program: 'duobao'
}, {
src: require('../../assets/activity/suocang/suocang.jpeg'),
title: '',
desc: '',
program: 'suocang'
}, {
src: require('../../assets/activity/huafeichognzhi/chargePhone.jpeg'),
title: '',
desc: 'ISCISCISCISCISCISC',
program: 'chargePhone'
}, {
src: require('../../assets/activity/health/health.jpeg'),
title: '',
desc: '',
program: 'onlineHealth'
}]
}
}
}



